import { useCallback } from 'react';
import { CollectionPageOnRowClickActionConfig, ResolvedAction } from '../types';
import { customAction, resolveAction } from '../utils/actions';
import { useActionsSDK } from './useActionsSDK';

export interface UseCollectionPageOnRowClickActionsParams {
  collectionPageOnRowClickActions?: CollectionPageOnRowClickActionConfig;
  collectionId: string;
}

export const useCollectionPageOnRowClickActions = ({
  collectionPageOnRowClickActions,
  collectionId,
}: UseCollectionPageOnRowClickActionsParams) => {
  const sdk = useActionsSDK({
    collectionId,
  });
  const buildCollectionPageActions = useCallback(
    (item, index) => {
      if (!collectionPageOnRowClickActions) {
        return;
      }

      const buildAction = (
        actionConfig: CollectionPageOnRowClickActionConfig,
      ) => {
        let actionToResolve: Partial<ResolvedAction> | null = null;
        const baseParams = {
          actionParams: { item, index },
          sdk,
        };
        switch (actionConfig.type) {
          case 'custom':
            actionToResolve = customAction({
              action: actionConfig,
              ...baseParams,
            });
            break;

          default:
            return null;
        }

        if (!actionToResolve) {
          return null;
        }

        const resolvedAction = resolveAction(actionConfig, actionToResolve);

        return resolvedAction.onClick?.();
      };

      return buildAction(collectionPageOnRowClickActions);
    },
    [collectionPageOnRowClickActions, sdk],
  );

  return buildCollectionPageActions;
};
